export default {
	data() {
		return {
			providerList: [],
			showPassword: true,
			showPassword1: true,
			isOk: false,
			n: 60,
			positionTop: 0,
			user:{
				
			}
		};
	},
	onShow() {
		let _this = this;
		uni.getStorage({
		    key: 'userInfo',
		    success: function (res) {
				if(res.data){
					_this.user = res.data;
					_this.user.img = '../../static/images/homeed.png';
				}
		    }
		});
	},
	methods: {
		clear(i) {
			switch (i) {
				case 1:
					this.account = '';
					break;
				case 2:
					this.realName = '';
					break;
				case 3:
					this.weixin = '';
					break;
				case 4:
					this.tenxun = '';
					break;
				case 5:
					this.telephone = '';
					break;
				case 6:
					this.picture = '';
					break;
				case 7:
					this.unit = '';
					break;
				default:
					break;
			}
		},
		look(i) {
			switch (i) {
				case 1:
					this.showPassword = !this.showPassword;
					break;
				case 2:
					this.showPassword1 = !this.showPassword1;
					break;
			}
		},
		initPosition() {
			/**
			 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			 * 反向使用 top 进行定位，可以避免此问题。
			 */
			this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
		},
		register() {
			/**
			 * 客户端对账号信息进行一些必要的校验。
			 * 实际开发中，根据业务需要进行处理，这里仅做示例。
			 */
			if (!this.user.realName) {
				uni.showToast({
					icon: 'none',
					title: '请输入姓名'
				});
				return;
			}
			if (!this.user.weixin) {
				uni.showToast({
					icon: 'none',
					title: '请输入微信'
				});
				return;
			}
			if (!this.user.tenxun) {
				uni.showToast({
					icon: 'none',
					title: '请输入QQ'
				});
				return;
			}
			var mPattern = /^1[3456789]\d{9}$/;
			if (!mPattern.test(this.user.telephone)) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的紧急电话'
				});
				return;
			}
			if (!this.user.picture) {
				uni.showToast({
					icon: 'none',
					title: '请输入个人情况'
				});
				return;
			}
			/**
			 * 下面简单模拟下服务端的处理
			 * 检测用户账号密码是否在已注册的用户列表中
			 * 实际开发中，使用 uni.request 将账号信息发送至服务端，客户端在回调函数中获取结果信息。
			 */
			// const data = this.user;
			const data = {
				realName: this.user.realName,
				weixin: this.user.weixin,
				tenxun: this.user.tenxun,
				telephone: this.user.telephone,
				picture: this.user.picture,
				unit: this.user.unit,
				id:this.user.id
			};
			const _this = this;
			console.log(JSON.stringify(data))
			let url = 'teacherUser/updateTeacherUser.json';
			this.uniHttp.getJSON(
				url,
				data,
				res => {
					console.log(res);
					if (res.data.success) {
						uni.setStorage({
							key:'userInfo',
							data:_this.user,
							success() {
								uni.showToast({
									icon: 'none',
									title: '修改用户信息成功'
								});
								uni.navigateBack({
									delta: 1
								});
							}
						})
					} else {
						uni.showToast({
							icon: 'none',
							title: '修改用户信息失败'
						});
					}
				},
				err => {
					uni.showToast({
						icon: 'none',
						title: '修改用户信息失败'
					});
				}
			);
		}
	}
};
